.switch {
  display: inline-block;
  width: 40px;
  height: 20px;
  background-color: #ccc;
  border-radius: 20px;
  position: relative;
}

.switch-checkbox {
  display: none;
}

.switch-label {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
  transition: background-color 0.3s;
}

.switch-label::before {
  content: "";
  display: block;
  width: 50%;
  height: 100%;
  background-color:  #717d7e ;
  border-radius: 20px;
  transition: transform 0.3s;
}

.switch.checked {
  background-color: #4CAF50;
}

.switch.checked .switch-label {
  background-color: #4CAF50;
}

.switch.checked .switch-label::before {
  transform: translateX(100%);
}

